.profile {
  height: 410px;
  background-image: linear-gradient(-45deg, transparent 25%, $clr_light-gray 25%, $clr_light-gray 50%, transparent 50%, transparent 75%, $clr_light-gray 75%, $clr_light-gray);
  background-size: 8px 8px;
  .title {
    margin: 0 auto;
    margin-bottom: 30px;
    width: 150px;
    height: 40px;
    border-radius: 2px;
    background: $clr_white;
    line-height: 40px;
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    color: $clr_gray;
  }
  .introduction {
    margin: 0 auto;
    width: 570px;
    .icon {
      text-align: center;
      max-width: 170px;
      border-radius: 50%;
    }
    .name {
      margin: 0 auto;
      font-size: 22px;
      color: $clr_gray;
      margin-bottom: 20px;
    }
    .position {
      margin-bottom: 20px;
      font-size: 18px;
      color: $clr_gray;
    }
    .description {
      color: $clr_gray;
      line-height: 30px;
    }
  }
  .link {
    display: block;
    margin: 0 auto;
    margin-top: 30px;
    padding: 0 20px;
    max-width: 270px;
    width: 80%;
    height: 40px;
    border-radius: 40px;
    line-height: 40px;
    background: $clr_orange;
    text-align: center;
    font-weight: bold;
    text-decoration: none;
    color: $clr_white;
    .symbol {
      width: 15px;
      margin-left: 8px;
    }
  }
}
.forms {
  height: 720px;
  padding-top: 50px;
  .title {
    margin-bottom: 30px;
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    color: $clr_gray;
  }
  .description {
    margin: 0 auto;
    margin-bottom: 30px;
    max-width: 600px;
    width: 80%;
    line-height: 30px;
    text-align: center;
    font-weight: bold;
    color: $clr_gray;
  }
  .form {
    max-width: 600px;
    width: 100%;
    margin: 0 auto;
    .label {
      display: none;
    }
    input, textarea {
      color: $clr_gray;
      font-size: 16px;
    }
    .name {
      margin-left: 20px;
      margin-bottom: 30px;
      padding-left: 20px;
      max-width: 500px;
      width: 90%;
      height: 40px;
      border-radius: 40px;
      outline: none;
      border: 0;
    }
    .content {
      margin-left: 20px;
      padding-left: 20px;
      padding-top: 20px;
      max-width: 500px;
      width: 90%;
      height: 200px;
      vertical-align: top;
      border-radius: 40px;
      resize: none;
      outline: none;
      border: 0;
    }
  }
  .confirm {
    display: block;
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 0 20px;
    width: 150px;
    height: 40px;
    border-radius: 40px;
    line-height: 40px;
    background: $clr_orange;
    text-align: center;
    font-weight: bold;
    text-decoration: none;
    color: $clr_white;
  }
  .top {
    display: block;
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 0 20px;
    width: 200px;
    height: 40px;
    border-radius: 40px;
    border: 1px solid $clr_orange;
    line-height: 40px;
    background: $clr_white;
    text-align: center;
    font-weight: bold;
    text-decoration: none;
    color: $clr_orange;
  }
}